// app/home/page.tsx (or Home page)
import { useEffect, useState } from "react";
import { PageContainer } from "@/components/layouts/page-container";
import TaskCard from "@/components/common/task-card";

// Dummy tasks data (Replace with actual data fetching logic)
const dummyTasks = [
  {
    id: "1",
    title: "Task 1",
    description: "Description 1",
    dueDate: "2024-12-23",
    status: "pending",
  },
  {
    id: "2",
    title: "Task 2",
    description: "Description 2",
    dueDate: "2024-12-20",
    status: "completed",
  },
  {
    id: "3",
    title: "Task 3",
    description: "Description 3",
    dueDate: "2024-12-25",
    status: "overdue",
  },
];

export default function Home() {
  const [tasks, setTasks] = useState(dummyTasks);

  // Update task logic
  const handleUpdateTask = (
    id: string,
    updates: Partial<(typeof dummyTasks)[0]>
  ) => {
    const updatedTasks = tasks.map((task) =>
      task.id === id ? { ...task, ...updates } : task
    );
    setTasks(updatedTasks);
  };

  // Filter tasks by status
  const pendingTasks = tasks.filter((task) => task.status === "pending");
  const completedTasks = tasks.filter((task) => task.status === "completed");
  const overdueTasks = tasks.filter((task) => task.status === "overdue");

  return (
    <PageContainer>
      <div className="space-y-8">
        <div>
          <h2 className="text-2xl font-semibold">Pending Tasks</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {pendingTasks.map((task) => (
              <TaskCard key={task.id} task={task} onUpdate={handleUpdateTask} />
            ))}
          </div>
        </div>

        <div>
          <h2 className="text-2xl font-semibold">Completed Tasks</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {completedTasks.map((task) => (
              <TaskCard key={task.id} task={task} onUpdate={handleUpdateTask} />
            ))}
          </div>
        </div>

        <div>
          <h2 className="text-2xl font-semibold">Overdue Tasks</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {overdueTasks.map((task) => (
              <TaskCard key={task.id} task={task} onUpdate={handleUpdateTask} />
            ))}
          </div>
        </div>
      </div>
    </PageContainer>
  );
}
